<template>
  <div id="app">
    <Header></Header>
    <Start v-show="status===0" @changeStatus="changeStatus"></Start>
    <Choose v-show="status===1||status===2" @changeStatus="changeStatus" @showResult="showResult"></Choose>
    <Status v-show="status===2" :result="result" :from="from"></Status>
    <Footer></Footer>
  </div>
</template>

<script>
import axios from 'axios'
import Header from './components/constructor/header.vue'
import Start from './components/start.vue'
import Choose from './components/choose.vue'
import Status from './components/status.vue'
import Footer from './components/constructor/footer.vue'
export default {
  name: 'app',
  data(){
    return{
      status:0, // 0->start 1->predding 2->success 3->fail
      result:0, // 0-> 未知 1->中一等奖 2->中二等奖 3->中三等奖 -1->没中奖
      from:-1
    }
  },
  components:{
    Header,
    Start,
    Choose,
    Status,
    Footer
  },
  methods:{
    changeStatus(status,open){
      this.$nextTick(()=>{
          this.status=status;
          this.from=open;
          console.log('open: ', open);
      })
    },
    showResult(result){
      this.result=result;
    },
  },
  // 刚进入页面时发起请求确定是否已经抽过奖
  mounted(){
    let that=this;
    // 检查localStorage里面是否有抽奖记录
    let isReward= window.localStorage.getItem('id')?true:false;
    if(!isReward){
      axios.get(`/loading`)
        .then(res=>{
          window.localStorage.setItem('id',res.data);
        }).catch(err=>{
          console.log(`the err happen when get id for ${err}`)
        })
    }
    // axios.post(`${that.HOME}/is-draw?openid=${openid}`)
    //   .then(res=>{
    //     console.log('res: ', res);
    //     if(res.data.code===0){

    //     }else{
    //       that.result=res.data.result;
    //       that.status=2;
    //     }
    //   }).catch(err=>{
    //     console.log(`the error happen when suerStatus by ${err}`)
    //   })
  }
}
</script>

