<template>
    <div class="choose container">
        <img class="title" src="../assets/clickTitle.png">
        <div class="brandContainer">
            <img :class="open===1?'open1':''" @click="clickBrand(1)" src="../assets/brand1.png">
            <img :class="open===2?'open2':''" @click="clickBrand(2)" src="../assets/brand2.png">
            <img :class="open===3?'open3':''" @click="clickBrand(3)" src="../assets/brand3.png">
        </div>
        <img class="magicWand" src="../assets/magicWand.png">
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            allowClick:true,
            open:0
        }
    },
    methods:{
        clickBrand(open){
            if(this.allowClick){
                let that=this;
                this.allowClick=false;
                this.open=open;
                let status = window.localStorage.getItem('status');
                if(!status){
                    // 发起请求获取成功或者失败
                    axios.post(`/luck-draw/${window.localStorage.getItem('id')}`)
                        .then(res=>{
                            that.$emit('showResult',res.data);
                            window.localStorage.setItem('status',res.data);
                        }).catch(err=>{
                            console.log(`error happen when get result by ${err}`)
                        })
                    setTimeout(()=>{
                        this.$emit("changeStatus",2,open);
                    },1000);
                }else{
                    this.$emit('showResult',window.localStorage.getItem('status'))
                }
            }
        }
    }
}
</script>

<style lang="less" scoped>
.choose{
    background-image: url("../assets/store.png");
    background-size: 100%;
    background-repeat: no-repeat;
    transform: scale(1.1) translateY(6vw);
    .title{
        width: 40vw;
        margin: 47vw 24vw 0vw 24vw;
    }
    .brandContainer{
        display: flex;
        justify-content: space-evenly;
        width: 84%;
        margin:5vw 8%;
        >img{
            width: 14vw;
        }
        @scale:2.6;
        @translateXLen:7vw;
        @translateYLen:6vw;
        .open1{
            animation: turn1 1s ease-in-out 0s 1 forwards;
            z-index: 10;
        }
        .open2{
            animation: turn2 1s ease-in-out 0s 1 forwards;
            z-index: 10;
        }
        .open3{
            animation: turn3 1s ease-in-out 0s 1 forwards;
            z-index: 10;
        }
        @keyframes turn1{
            0%{
                transform: translate(0vw,0vw) rotateY(0deg) scale(1);
            }
            100%{
                transform:translate(@translateXLen,-@translateYLen) rotateY(90deg) scale(@scale);
            }
        }
        @keyframes turn2{
            0%{
                transform:translateY(0vw) rotateY(0deg) scale(1);
            }
            100%{
                transform:translateY(-@translateYLen) rotateY(90deg) scale(@scale);
            }
        }
        @keyframes turn3{
            0%{
                transform: translate(0vw,0vw) rotateY(0deg) scale(1);
            }
            100%{
                transform:translate(-@translateXLen,-@translateYLen) rotateY(90deg) scale(@scale);
            }
        }
    }
    .magicWand{
        width: 12vw;
        margin: 3vw 38vw;

    }
}
</style>
