<template>
    <div :class="['status','container','turn'+from]">
        <img class="claws leftClaws" src="../assets/claws2.png">
        <img v-if="result>0" class="statusImg" src="../assets/success.gif">
        <img v-if="result<0" class="statusImg" src="../assets/fail.gif">
        <p class="resultTip">{{result | resultTip}}</p>
        <p class="rewardTip">{{result | rewardTip}}</p>
        <img class="claws rightClaws" src="../assets/claws2.png">
    </div>
</template>

<script>
export default {
    props:["result","from"],
    filters:{
        resultTip:function(val){
            if(val===1){
                return '恭喜获得一等奖';
            }else if(val===2){
                return '恭喜获得二等奖';
            }else if(val===3){
                return '恭喜获得三等奖';
            }else if(val===-1){
                return '很遗憾，没有中奖'
            }
        },
        rewardTip:function(val){
            if(val>0){
                return '请在晚会结束后领取奖品';
            }
        }
    }
}
</script>

<style lang="less" scoped>
.status{
    position: absolute;
    top: 24vw;
    left: 6vw;
    background-image: url("../assets/brandBg.png");
    background-size: 100%;
    background-repeat: no-repeat;
    .claws{
        width: 80%;
        margin: 5vw 5%;
    }
    .rightClaws{
        float: right;
    }
    .statusImg{
        position: absolute;
        top: 16vw;
        left: 0vw;
        width: 100%;
    }
    p{
        margin: 0px;
        margin-top: 4vw;
        text-align: center;
        font-family: '黑体';
        letter-spacing: 1.5vw;
        text-shadow:#6499a9 2px 0 0,#6499a9 0 2px 0,#6499a9 -2px 0 0,#6499a9 0 -2px 0;
        color: white;
    }
    .resultTip{
        margin-top: 43vw;
        font-size: 8vw;
    }
    .rewardTip{
        font-size: 6vw;
    }
}

@translateXLen:15vw;
    @translateYLen:13vw;
    .turn1{
        animation: showAni1 1s ease-in-out 0s 1 forwards;
    }
    .turn2{
        animation: showAni2 1s ease-in-out 0s 1 forwards;
    }
    .turn3{
        animation: showAni3 1s ease-in-out 0s 1 forwards;
    }
    @keyframes showAni1{
        0%{
            transform: translate(-@translateXLen,@translateYLen) scale(0.5) rotateY(-90deg);
        }
        100%{
            transform: translate(0vw,0vw) scale(1.1) rotateY(0deg);
        }
    }
    @keyframes showAni2{
        0%{
            transform: translate(0vw,@translateYLen)  scale(0.5) rotateY(-90deg);
        }
        100%{
            transform: translate(0vw,0vw) scale(1.1) rotateY(0deg);
        }
    }
    @keyframes showAni3{
        0%{
            transform: translate(@translateXLen,@translateYLen) scale(0.5) rotateY(-90deg);
        }
        100%{
            transform: translate(0vw,0vw) scale(1.1) rotateY(0deg);
        }
    }
</style>
